body {
  background: linear-gradient(270deg, silver 0%, #fcfeff 100%);
}

.breed-select {
  text-align: center;
  border: 2px solid #404040;
  width: 200px;
  height: 22px;
  font-size: 18px;
}

.cat-info {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: 30px;
  display: flex;
}

.cat-desc {
  width: 500px;
}

img {
  width: 300px;
}

.loader-wrapper {
  justify-content: center;
  display: flex;
}

.loader {
  box-sizing: border-box;
  border-top: 4px solid #fff;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  width: 48px;
  height: 48px;
  margin-right: 20px;
  animation: 1s linear infinite rotation;
  display: inline-block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #606060;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: .5s linear infinite reverse rotation;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

